import {LitElement, html, css, customElement, property} from 'lit-element';
import {isMobile} from '../../../utils/response-query';
import '../../molecules/uc-header-list-popup/uc-header-list-popup';

@customElement('uc-header-shop-list')
class UcHeaderShopList extends LitElement {
    @property({type: Number}) count = 0;
    @property({type: String}) dataList;

    static styles = css`
        :host {
            position: relative;
            display: flex;
            align-items: center; }
        @media (min-width: 992px) {
        :host {
                padding: 10px;
                border-top: 1px solid transparent;
                border-left: 1px solid transparent;
                border-right: 1px solid transparent;
                z-index: 3;
                background-color: #fff; } }
        @media (max-width: 991px) {
        :host {
                overflow: hidden; } }
        @media (min-width: 992px) {
        :host(:hover) {
                border-top: 1px solid #eee;
                border-left: 1px solid #eee;
                border-right: 1px solid #eee; }
        :host(:hover) uc-header-list-popup {
                display: flex; }
        :host(:hover)::after {
                position: absolute;
                content: '';
                height: 1px;
                width: 100%;
                left: 0;
                bottom: 0;
                z-index: 4;
                background-color: #fff; } }
        
        ::slotted(a) {
            height: 100%;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0; }
        
        .shop-list-icon {
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat; }
        @media (min-width: 992px) {
        .shop-list-icon {
                width: 26px;
                height: 32px;
                background-image: url(data:image/svg+xml;base64,); } }
        @media (max-width: 991px) {
        .shop-list-icon {
                background-image: url(data:image/svg+xml;base64,); } }
        @media (max-width: 991px) and (min-width: 768px) {
        .shop-list-icon {
                width: 19px;
                height: 23px; } }
        @media (max-width: 767px) {
        .shop-list-icon {
                width: 1.35714em;
                height: 1.64286em; } }
        
        .shop-list-text {
            font-size: 11px;
            font-weight: bold;
            line-height: 1.27;
            width: 53px;
            text-transform: uppercase;
            color: #333333;
            margin-left: 10px;
            margin-right: 10px; }
        @media (max-width: 991px) {
        .shop-list-text {
                display: none; } }
        
        .shop-list-tooltip {
            width: 14px;
            height: 14px;
            background-color: #66c05d;
            border-radius: 50%;
            color: #ffffff;
            font-size: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            align-self: flex-end;
            position: relative;
            user-select: none;
            margin-bottom: 3px; }
        @media (max-width: 991px) {
        .shop-list-tooltip {
                display: none; } }
        .shop-list-tooltip::after {
            display: none;
            content: attr(data-text);
            background-color: rgba(0, 0, 0, 0.7);
            border-radius: 3px;
            padding: 5px;
            position: absolute;
            bottom: 14px;
            right: 14px;
            width: 260px;
            font-size: 12px; }
        .shop-list-tooltip:hover::after {
            display: block; }
        
        uc-header-list-popup {
            z-index: -1;
            position: absolute;
            top: calc(100% - 1px);
            right: -1px;
            display: none; }
    `;

    render() {
        return html`
            <slot></slot>
            <div class="shop-list-icon" data-count="${this.count}"></div>
            ${isMobile() ? html`` : html`
                <div class="shop-list-text">СПИСОК ПОКУПОК</div>
                <div class="shop-list-tooltip"
                     data-text="Просто добавьте сюда товары, распечатайте список и пользуйтесь им в магазине">?</div>
                <uc-header-list-popup dataType="shop-list" dataList="${this.dataList}"></uc-header-list-popup>
            `}
        `;
    }
}